
@import "mixin/mixins";
@import "mixin/normal";

.settings_menu {
	background: #fff;
	margin-top: 30px;
	li {
		a {
			color: #757575;
			font-size: 14px;
			font-weight: 400;
			padding: 15px 30px;
			transition: 0.3s;
			position: relative;
			&:after {
				content: '';
				display: block;
				position: absolute;
				height: 3px;
				width: 0%;
				bottom: 0;
				left: 0;
				transition: 0.5s;
				background: $yellow; }

			&:hover {
				&:after {
					width: 100%; } } } }


	li.active {
		&:after {
			content: '';
			display: block;
			position: absolute;
			height: 3px;
			width: 100%;
			bottom: 0;
			left: 0;
			background: darken($yellow,10%); } } }

form.bussiness {
	background: #fff;
	padding: 30px;
	margin: 20px 0;
	label {
		color: #757575;
		font-size: 12px;
		font-weight: 400; }
	input,
	select {
		border: 1px solid #dadada;
		border-radius: 5px;
		width: 100%;
		height: 30px;
		display: block;
		margin: 5px 0 10px;
		padding: 5px 10px;
		font-size: 12px; }
	.input_red {
		input {
			border-color: red; } }

	sup {
		color: red; }

	.required_fields {
		color: #757575;
		font-size: 12px;
		font-weight: 300; }

	.button_container {} }

button.save,
button.change_password,
button.check {
	display: inline-block;
	font-size: 14px;
	font-weight: 600;
	color: white;
	background: $yellow;
	border-radius: 50px;
	padding: 7px 35px;
	margin-top: 30px;
	margin-right: 10px;
	&:hover {
		background: #ffd453; } }

button.check {
	margin-top: -10px;
	margin-left: 15px; }
